body {
  font-family: "Inter", "Arial", sans-serif;
  font-size: 19px;
  color: rgb(150, 154, 160);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  background-color: rgb(255, 202, 66);
  border: none;
  color: rgb(27, 55, 100);
  font-family: "Cardo", "Times New Roman", sans-serif;
  font-weight: 400;
  padding: 19px;
  cursor: pointer;
  font-size: 19px;
  line-height: 28px;
}
.button--light {
  background-color: inherit;
  border: 2px solid rgb(255, 202, 66);
}
.button:hover {
  background-color: rgb(27, 55, 100);
  color: rgb(255, 202, 66);
  outline: 1px solid rgb(255, 202, 66);
}

.page {
  height: 100%;
}

.page__body {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  width: 1440px;
  box-sizing: border-box;
}

.page__header {
  padding: 0 70px 100px;
  background-color: rgb(27, 55, 100);
}

.page__main {
  flex-grow: 1;
}

.page__footer {
  padding: 93px 70px 0;
}

.title {
  display: block;
  font-family: "Cardo", "Times New Roman", sans-serif;
  margin: 0;
}
.title--h1 {
  color: rgb(255, 255, 255);
  font-size: 64px;
  line-height: 78px;
}
.title--main {
  position: relative;
  padding-bottom: 23px;
}
.title--main::before {
  position: absolute;
  bottom: 0;
  width: 50px;
  height: 3px;
  background-color: rgb(255, 202, 66);
  content: "";
}
.title--center::before {
  left: 50%;
  transform: translateX(-50%);
}

.main__promo {
  padding: 0 70px 100px;
}

.main__books {
  padding: 110px 70px 90px;
}

.main__author {
  padding: 180px 70px 140px;
}

.main__trust {
  padding: 110px 70px 130px;
}

.main__order {
  padding: 125px 70px;
}

.main__benefits {
  padding: 140px 70px;
}

.main__trail {
  padding: 100px 70px 120px;
}

.main__reviews {
  padding: 130px 70px 150px;
}

.main__articles {
  padding: 110px 70px 150px;
}

.main__subscribe {
  padding: 120px 70px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__order-button {
  width: 222px;
}

.nav__list {
  display: flex;
  gap: 30px;
  flex-wrap: wrap;
  min-height: 109px;
  padding: 20px 0;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.nav__link {
  display: block;
  padding: 40px 10px;
  color: rgb(246, 248, 252);
  text-decoration: none;
  font-size: 17px;
  line-height: 25px;
}
.nav__link:hover {
  color: rgb(255, 202, 66);
}
.nav__link:active {
  color: rgba(180, 199, 231, 0.2);
}

.parameters {
  display: flex;
  gap: 70px;
}

.parameters__wrapper {
  display: grid;
  grid-template-columns: repeat(2, max-content);
  column-gap: 10px;
  row-gap: 12px;
  align-items: center;
  margin: 0;
}
.parameters__wrapper::before {
  display: block;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: rgb(255, 202, 66);
  content: "";
}

.parameters__item {
  color: rgb(255, 255, 255);
  font-family: "Cardo", "Times New Roman", sans-serif;
  font-size: 24px;
  line-height: 29px;
}

.parameters__value {
  margin: 0;
  grid-column: 2/2;
  justify-self: end;
  color: rgb(180, 199, 231);
  font-size: 17px;
  line-height: 27px;
}

.promo {
  display: flex;
  gap: 102px;
  background-color: rgb(27, 55, 100);
}

.promo__image {
  box-shadow: -1px 2px 11px rgba(0, 0, 0, 0.2);
}

.promo-text {
  align-self: center;
  position: relative;
  margin-left: 23px;
}

.promo-text__note {
  display: block;
  margin-bottom: 15px;
  padding-left: 49px;
  font-family: "Cardo", "Times New Roman", sans-serif;
  color: rgb(255, 255, 255);
  font-size: 21px;
  line-height: 36px;
}
.promo-text__note::before {
  position: absolute;
  left: 0;
  top: 17px;
  display: block;
  width: 35px;
  height: 2px;
  background-color: rgb(255, 202, 66);
  content: "";
}

.promo-text__title {
  margin-bottom: 24px;
}

.promo-text__description {
  margin: 0;
  margin-bottom: 42px;
  color: rgb(180, 199, 231);
  font-size: 18px;
  line-height: 28px;
}

.promo-text__wrapper {
  display: flex;
  align-items: center;
  gap: 51px;
  margin-bottom: 47px;
}

.promo-text__link {
  position: relative;
  display: block;
  color: rgb(255, 255, 255);
  font-family: "Cardo", "Times New Roman", sans-serif;
  text-decoration: none;
  font-size: 19px;
  line-height: 30px;
}
.promo-text__link::before {
  position: absolute;
  bottom: 0;
  display: block;
  width: 100%;
  height: 1px;
  background-color: rgb(255, 255, 255);
  content: "";
}
.promo-text__link:hover {
  color: rgb(255, 202, 66);
}
.promo-text__link:hover::before {
  background-color: rgb(255, 202, 66);
}
.promo-text__link:active {
  color: rgba(255, 255, 255, 0.2);
}
.promo-text__link:active::before {
  background-color: rgba(255, 255, 255, 0.2);
}

.promo-text__order-button {
  width: 222px;
}

.socials__list {
  display: flex;
  gap: 10px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.socials__link {
  position: relative;
  display: block;
  width: 45px;
  height: 45px;
  background-color: rgb(255, 255, 255);
  color: rgb(20, 20, 20);
}
.socials__link--light {
  border: 1px solid rgb(255, 202, 66);
  background-color: inherit;
}
.socials__link:hover {
  background-color: rgb(255, 202, 66);
}
.socials__link:active {
  opacity: 0.2;
}

.socials__icon {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}
.socials__icon--light {
  color: rgb(255, 255, 255);
}

.books__title {
  margin: 0;
  margin-bottom: 60px;
  color: rgb(27, 55, 100);
  text-align: center;
  font-size: 42px;
  line-height: 50px;
}

.books__list {
  display: flex;
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.book-item {
  display: flex;
  gap: 30px;
}

.book-item__title {
  display: block;
  margin: 38px 0 7px 0;
  color: rgb(27, 55, 100);
  font-size: 32px;
  line-height: 38px;
}

.book-item__wrapper {
  display: flex;
  flex-direction: column;
}

.book-item__price {
  margin-bottom: 17px;
  color: rgb(255, 202, 66);
  font-size: 21px;
  line-height: 33px;
}

.book-item__description {
  display: block;
  margin-bottom: 13px;
  font-size: 19px;
  line-height: 28px;
}

.book-item__property {
  display: flex;
  gap: 10px;
  align-items: center;
  margin-bottom: 27px;
  color: rgb(27, 55, 100);
  font-family: "Cardo", "Times New Roman", sans-serif;
  font-size: 24px;
  line-height: 28px;
}
.book-item__property::before {
  display: block;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: rgb(255, 202, 66);
  content: "";
}

.book-item__order-button {
  max-width: 206px;
}

.book-item__image {
  box-shadow: 0 0 11px rgba(0, 0, 0, 0.2);
}

.author {
  position: relative;
  display: flex;
  gap: 150px;
  background-color: rgb(245, 248, 252);
}

.author__wrapper {
  width: 605px;
}

.author__title {
  margin-bottom: 33px;
  color: rgb(27, 55, 100);
  font-size: 42px;
  line-height: 50px;
}

.author__about {
  margin-bottom: 34px;
  font-size: 19px;
  line-height: 34px;
}

.author__numbers {
  margin-bottom: 70px;
}

.author__photo {
  position: absolute;
  top: 150px;
  left: 40px;
}

.author__frame {
  display: block;
  width: 525px;
  height: 620px;
  border: 10px solid rgb(255, 202, 66);
}

.author__image {
  box-shadow: 0 0 20px rgba(27, 55, 100, 0.3);
}

.numbers {
  display: flex;
  gap: 26px;
}

.numbers__wrapper {
  display: flex;
  flex-direction: column;
}

.numbers__item {
  color: rgb(27, 55, 100);
  font-family: "Cardo", "Times New Roman", sans-serif;
  font-size: 54px;
  line-height: 77px;
}

.numbers__value {
  margin: 0;
  font-size: 17px;
  line-height: 21px;
}

.qr {
  display: flex;
  gap: 40px;
  padding: 15px 0 16px 20px;
  background-color: rgb(27, 55, 100);
  width: 484px;
  box-sizing: border-box;
}

.qr__description {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.qr__author {
  margin-bottom: 11px;
  color: rgb(255, 202, 66);
  font-family: "Cardo", "Times New Roman", sans-serif;
  font-size: 22px;
  line-height: 26px;
}

.qr__mail,
.qr__phone {
  font-size: 18px;
  line-height: 28px;
}

.trust__title {
  margin-bottom: 76px;
  color: rgb(27, 55, 100);
  text-align: center;
  font-size: 42px;
  line-height: 50px;
}

.trust__list {
  display: flex;
  text-align: center;
  gap: 37px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.trust-item::before {
  margin: 0 auto;
  margin-bottom: 22px;
}

.trust-item--amazen::before {
  display: block;
  width: 189px;
  height: 100px;
  background-image: url("../img/amazin.svg");
  background-position: center;
  content: "";
}
.trust-item--megan::before {
  display: block;
  width: 189px;
  height: 100px;
  background-image: url("../img/megan.svg");
  background-position: center;
  content: "";
}
.trust-item--megank::before {
  display: block;
  width: 189px;
  height: 100px;
  background-image: url("../img/megank.svg");
  background-position: center;
  content: "";
}
.trust-item--urban::before {
  display: block;
  width: 189px;
  height: 100px;
  background-image: url("../img/urban.svg");
  background-position: center;
  content: "";
}

.trust-item__title {
  margin-bottom: 19px;
  color: rgb(27, 55, 100);
  font-size: 24px;
  line-height: 29px;
}

.trust-item__description {
  font-size: 19px;
  line-height: 28px;
}

.order {
  background-color: rgb(27, 55, 100);
  background-image: url("../img/bg/books.png");
  background-position: top 70px right 70px;
  background-repeat: no-repeat;
  background-size: 560px 394px;
}
@media screen and (min-resolution: 2dppx) {
  .order {
    background-image: url("../img/bg/books@2x.png");
  }
}

.order__title {
  position: relative;
  margin-bottom: 27px;
  padding-bottom: 24px;
  color: rgb(255, 255, 255);
  font-size: 45px;
  line-height: 54px;
}
.order__title::before {
  position: absolute;
  bottom: 0;
  width: 50px;
  height: 3px;
  background-color: rgb(255, 202, 66);
  content: "";
}

.order__description {
  margin: 0;
  margin-bottom: 27px;
  width: 610px;
  color: rgb(180, 199, 231);
  font-size: 19px;
  line-height: 28px;
}

.order__button {
  min-width: 220px;
  color: rgb(255, 255, 255);
}

.benefits {
  position: relative;
}

.benefits__title {
  margin-bottom: 90px;
  color: rgb(27, 55, 100);
  text-align: center;
  font-size: 42px;
  line-height: 50px;
}

.benefits__wrapper {
  display: flex;
  gap: 109px;
  align-items: start;
}

.benefits__list {
  display: grid;
  grid-template-columns: repeat(2, 303px);
  gap: 30px;
  counter-reset: benefits;
  margin: 0;
  padding: 0;
  list-style-type: none;
  padding-top: 30px;
}

.benefits__frame {
  display: block;
  width: 535px;
  height: 632px;
  border: 10px solid rgb(255, 202, 66);
}

.benefits__image {
  position: absolute;
  top: 283px;
  right: 110px;
}

.benefits-item {
  padding: 30px;
  background-color: rgb(244, 248, 255);
  width: 303px;
  height: 272px;
  box-sizing: border-box;
}
.benefits-item::before {
  display: block;
  margin-bottom: 30px;
  padding-top: 10px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: rgb(255, 202, 66);
  color: rgb(27, 55, 100);
  font-family: "Cardo", "Times New Roman", sans-serif;
  text-align: center;
  box-sizing: border-box;
  counter-increment: benefits;
  content: "0" counter(benefits);
  font-size: 23px;
  line-height: 29px;
}

.benefits-item__description {
  margin: 0;
  color: rgb(27, 55, 100);
  font-family: "Cardo", "Times New Roman", sans-serif;
  font-size: 24px;
  line-height: 33px;
}

.trail {
  background-color: rgb(244, 248, 255);
}

.trail__title {
  margin-bottom: 96px;
  color: rgb(27, 55, 100);
  text-align: center;
  font-size: 42px;
  line-height: 50px;
}

.trail__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin-bottom: 70px;
}

.trail__button {
  display: block;
  margin: 0 auto;
  margin-bottom: 34px;
  width: 429px;
}

.trail__note {
  display: block;
  margin: 0 auto;
  width: max-content;
  text-align: center;
}

.trail__link {
  color: rgb(27, 55, 100);
  text-decoration: none;
}
.trail__link:hover {
  color: rgb(255, 202, 66);
}
.trail__link:active {
  color: rgb(27, 55, 100);
  opacity: 0.2;
}

.trail-item {
  padding: 40px;
  background-color: rgb(255, 255, 255);
  box-sizing: border-box;
}

.trail-item__title {
  color: rgb(27, 55, 100);
  margin-bottom: 21px;
  font-size: 24px;
  line-height: 29px;
}

.trail-item__description {
  margin-bottom: 10px;
  font-size: 19px;
  line-height: 32px;
}

.trail-properties {
  display: flex;
  gap: 45px;
}

.trail-properties__wrapper {
  display: flex;
  gap: 10px;
  align-items: end;
}

.trail-properties__item {
  display: flex;
  gap: 10px;
  align-items: center;
  color: rgb(27, 55, 100);
  font-family: "Cardo", "Times New Roman", sans-serif;
  font-size: 22px;
  line-height: 30px;
}
.trail-properties__item::before {
  display: block;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: rgb(255, 202, 66);
  content: "";
}

.trail-properties__value {
  margin: 0;
  font-size: 17px;
  line-height: 27px;
}

.reviews {
  display: flex;
  gap: 63px;
}

.reviews__text-wrapper {
  width: 468px;
}

.reviews__title {
  margin-bottom: 33px;
  color: rgb(27, 55, 100);
  font-size: 42px;
  line-height: 50px;
}

.reviews__description {
  margin: 0;
  margin-bottom: 14px;
  font-size: 19px;
  line-height: 34px;
}

.reviews__list {
  display: grid;
  grid-template-columns: repeat(2, 381px);
  gap: 30px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.reviews__item:nth-child(2) {
  grid-row: 1/span 2;
  grid-column: 2/span 1;
  align-self: center;
}

.reviews-item {
  padding: 30px 43px 24px 35px;
  width: 381px;
  height: 292px;
  background-color: rgb(27, 55, 100);
  box-sizing: border-box;
}

.reviews-item__wrapper {
  display: flex;
  gap: 20px;
}

.reviews-item__avatar {
  width: 85px;
  height: 85px;
}

.reviews-item__name {
  display: block;
  margin-bottom: 15px;
  color: rgb(255, 255, 255);
  font-family: "Cardo", "Times New Roman", sans-serif;
  font-size: 24px;
  line-height: 29px;
}

.reviews-item__quote {
  color: rgb(255, 255, 255);
  font-family: "Cardo", "Times New Roman", sans-serif;
  font-size: 22px;
  line-height: 33px;
}

.reviews-item__description {
  margin: 0;
  color: rgb(180, 199, 231);
  font-size: 18px;
  line-height: 29px;
}

.rating__numbers {
  display: block;
  font-family: "Cardo", "Times New Roman", sans-serif;
  color: rgb(27, 55, 100);
  font-size: 22px;
  line-height: 26px;
}

.rating__note {
  display: block;
  color: rgb(27, 55, 100);
  font-size: 18px;
  line-height: 28px;
}

.articles {
  background-color: rgb(244, 248, 255);
}

.articles__title {
  margin-bottom: 60px;
  color: rgb(27, 55, 100);
  text-align: center;
  font-size: 42px;
  line-height: 50px;
}

.articles__list {
  display: grid;
  grid-template-columns: repeat(3, 420px);
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.articles-item__image {
  margin: 0;
  width: 420px;
  height: 350px;
}

.articles-item__wrapper {
  padding: 25px 30px 30px;
  background-color: rgb(255, 255, 255);
  border-radius: 0 0 5px 5px;
}

.articles-item__title {
  margin-bottom: 19px;
  color: rgb(27, 55, 100);
  font-size: 24px;
  line-height: 34px;
}

.articles-item__description {
  margin: 0;
  margin-bottom: 13px;
  font-size: 19px;
  line-height: 30px;
}

.articles-item__more {
  display: flex;
  justify-content: space-between;
}

.articles-item__link {
  position: relative;
  color: rgb(27, 55, 100);
  font-family: "Cardo", "Times New Roman", sans-serif;
  text-decoration: none;
  font-size: 19px;
  line-height: 23px;
}
.articles-item__link::after {
  position: absolute;
  bottom: 0;
  display: block;
  width: 100%;
  height: 2px;
  background-color: rgb(27, 55, 100);
  content: "";
}
.articles-item__link:hover {
  color: rgb(255, 202, 66);
}
.articles-item__link:hover::after {
  background-color: rgb(255, 202, 66);
}
.articles-item__link:active {
  color: rgba(27, 55, 100, 0.4);
}
.articles-item__link:active::after {
  background-color: rgba(27, 55, 100, 0.4);
}

.articles-item__date {
  color: rgb(27, 55, 100);
  font-size: 16px;
  line-height: 25px;
}

.subscribe__wrapper {
  padding: 100px 150px 115px;
  background-color: rgb(255, 202, 66);
}

.subscribe__title {
  margin-bottom: 51px;
  color: rgb(27, 55, 100);
  text-align: center;
  font-size: 42px;
  line-height: 50px;
}
.subscribe__title::before {
  background-color: rgb(27, 55, 100);
}

.subscribe__description {
  margin: 0;
  margin-bottom: 50px;
  color: rgb(27, 55, 100);
  text-align: center;
  font-size: 18px;
  line-height: 28px;
}

.subscribe-form {
  display: flex;
  gap: 16px;
}

.subscribe-form__label {
  flex-grow: 1;
}

.subscribe-form__input {
  width: 100%;
  height: 100%;
  padding: 19px 39px 20px;
  background-color: rgb(255, 255, 255);
  border: none;
  box-sizing: border-box;
  font-size: 18px;
  line-height: 30px;
}

.subscribe-form__button {
  min-width: 328px;
  color: rgb(255, 255, 255);
  background-color: rgb(27, 55, 100);
  font-family: "Inter", "Arial", sans-serif;
  font-size: 18px;
  line-height: 30px;
}

.subscribe-form__input::placeholder {
  color: rgb(180, 199, 231);
  font-size: 18px;
  line-height: 30px;
}

.footer {
  background-color: rgb(27, 55, 100);
}

.footer__wrapper {
  display: flex;
  gap: 30px;
  margin-bottom: 61px;
}

.footer__logo {
  margin-bottom: 20px;
}

.footer-column {
  padding: 0 30px;
}

.footer-column__title {
  margin-bottom: 14px;
  color: rgb(255, 255, 255);
  font-size: 24px;
  line-height: 33px;
}

.footer-column__list {
  display: flex;
  flex-direction: column;
  gap: 9px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.footer-column__link {
  color: rgb(180, 199, 231);
  text-decoration: none;
  font-size: 18px;
  line-height: 30px;
}
.footer-column__link:hover {
  color: rgb(255, 202, 66);
}
.footer-column__link:active {
  color: rgba(180, 199, 231, 0.2);
}

.contacts {
  display: grid;
  grid-template-columns: repeat(2, max-content);
}

.contacts__item {
  color: rgb(255, 255, 255);
  font-family: "Cardo", "Times New Roman", sans-serif;
  font-size: 18px;
  line-height: 27px;
}

.contacts__value {
  color: rgb(180, 199, 231);
  font-size: 17px;
  line-height: 27px;
